// tests/layouts/AuthLayout.test.tsx
import React from 'react';
import { render } from '@testing-library/react-native';
import { Text, View } from 'react-native';
import AuthLayout from '../../src/layouts/AuthLayout';

// useTheme 모킹
jest.mock('../../src/hooks/useTheme', () => ({
  useTheme: () => ({
    theme: {
      colors: {
        background: '#ffffff',
      },
      dark: false,
    },
  }),
}));

// LoadingIndicator 모킹
jest.mock('../../src/components/LoadingIndicator', () => {
  const React = require('react');
  const { View } = require('react-native');
  return {
    __esModule: true,
    default: () => React.createElement(View, { testID: 'loading-indicator' }),
  };
});

// 이미지 요청 모킹
jest.mock('../../src/assets/images/logo.png', () => ({}), { virtual: true });
jest.mock('../../src/assets/images/auth-background.jpg', () => ({}), { virtual: true });

describe('AuthLayout', () => {
  // 기본 렌더링 테스트
  it('renders children correctly', () => {
    const { getByText } = render(
      <AuthLayout>
        <Text>인증 내용</Text>
      </AuthLayout>
    );
    
    expect(getByText('인증 내용')).toBeTruthy();
  });
  
  // 로딩 상태 테스트
  it('shows loading indicator when loading is true', () => {
    const { getByTestId, queryByText } = render(
      <AuthLayout loading={true}>
        <Text>인증 내용</Text>
      </AuthLayout>
    );
    
    expect(getByTestId('loading-indicator')).toBeTruthy();
    expect(queryByText('인증 내용')).toBeNull();
  });
  
  // 제목 및 푸터 테스트
  it('renders title and footer when provided', () => {
    const { getByText } = render(
      <AuthLayout
        title={<Text>인증 제목</Text>}
        footer={<Text>인증 푸터</Text>}
      >
        <Text>인증 내용</Text>
      </AuthLayout>
    );
    
    expect(getByText('인증 제목')).toBeTruthy();
    expect(getByText('인증 푸터')).toBeTruthy();
    expect(getByText('인증 내용')).toBeTruthy();
  });
  
  // 배경 이미지 테스트
  it('renders with background image when imageBackground is true', () => {
    const { getByTestId, getByText } = render(
      <AuthLayout imageBackground={true}>
        <Text>인증 내용</Text>
      </AuthLayout>
    );
    
    expect(getByTestId('image-background')).toBeTruthy();
    expect(getByText('인증 내용')).toBeTruthy();
  });
  
  // 로고 숨김 테스트
  it('does not render logo when logoVisible is false', () => {
    const { queryByTestId, getByText } = render(
      <AuthLayout logoVisible={false}>
        <Text>인증 내용</Text>
      </AuthLayout>
    );
    
    expect(queryByTestId('logo-image')).toBeNull();
    expect(getByText('인증 내용')).toBeTruthy();
  });
  
  // 기본 배경 테스트
  it('renders with default background when imageBackground is false', () => {
    const { queryByTestId, getByText } = render(
      <AuthLayout imageBackground={false}>
        <Text>인증 내용</Text>
      </AuthLayout>
    );
    
    expect(queryByTestId('image-background')).toBeNull();
    expect(getByText('인증 내용')).toBeTruthy();
  });

  // 추가 테스트 케이스
  it('renders logo when logoVisible is true', () => {
    const { getByTestId } = render(
      <AuthLayout logoVisible={true}>
        <Text>인증 내용</Text>
      </AuthLayout>
    );
    
    expect(getByTestId('logo-image')).toBeTruthy();
  });
  
  it('handles complex children correctly', () => {
    const { getByText } = render(
      <AuthLayout>
        <View>
          <Text>첫 번째 자식</Text>
          <Text>두 번째 자식</Text>
          <View>
            <Text>중첩된 자식</Text>
          </View>
        </View>
      </AuthLayout>
    );
    
    expect(getByText('첫 번째 자식')).toBeTruthy();
    expect(getByText('두 번째 자식')).toBeTruthy();
    expect(getByText('중첩된 자식')).toBeTruthy();
  });
  
  it('renders correctly without title and footer', () => {
    const { getByText, queryByTestId } = render(
      <AuthLayout>
        <Text>인증 내용</Text>
      </AuthLayout>
    );
    
    expect(getByText('인증 내용')).toBeTruthy();
    // 제목과 푸터가 없으므로 이 요소들이 없어야 함
    expect(queryByTestId('title-container')).toBeNull();
    expect(queryByTestId('footer-container')).toBeNull();
  });
  
  it('renders correctly with only title', () => {
    const { getByText, queryByTestId } = render(
      <AuthLayout title={<Text>인증 제목만</Text>}>
        <Text>인증 내용</Text>
      </AuthLayout>
    );
    
    expect(getByText('인증 제목만')).toBeTruthy();
    expect(getByText('인증 내용')).toBeTruthy();
    expect(queryByTestId('footer-container')).toBeNull();
  });
  
  it('renders correctly with only footer', () => {
    const { getByText, queryByTestId } = render(
      <AuthLayout footer={<Text>인증 푸터만</Text>}>
        <Text>인증 내용</Text>
      </AuthLayout>
    );
    
    expect(getByText('인증 푸터만')).toBeTruthy();
    expect(getByText('인증 내용')).toBeTruthy();
    expect(queryByTestId('title-container')).toBeNull();
  });
});